<div class="mt2" id="Sites" v-if="sites"><div class="nearest" v-if="home"><h3>Nearest groups</h3><div class="site pointer glow o-70" v-for="site in nearestSites" @click="click(site)" :class="{ selected: selected(site) }"><div class="name dib w-70 pa1">{{ site.properties['Name'].replace(/ Good Karma Network/i, '') }}</div><div class="distance dib w-30 ph1 pv1">{{ Math.round(site.distance) }} km</div></div></div><div class="all" v-else><h3>All groups</h3><div class="site pointer glow o-70" v-for="site in alphabetisedSites" @click="click(site)" :class="{ selected: selected(site) }"><div class="name dib w-70 pa1">{{ site.properties['Name'].replace(/ Good Karma Network/i, '') }}</div></div></div></div>